import React from "react";
import SidebarItem from "./SidebarItem.js";

class Sidebar extends React.Component {
	render() {
		return (
			<div className="green vheight-100 rounded mt-3 control-panel col">
				<h6 className="pt-2">Visualizza come:</h6>
				<div className="btn-group btn-group-toggle" data-toggle="buttons">
					<label
						className={
							this.props.selectedMode == "raw"
								? "btn btn-success border active"
								: "btn btn-success border"
						}
					>
						<input
							type="radio"
							defaultChecked={this.props.selectedMode == "raw"}
							onClick={() => {
								this.props.changeMode("raw");
								this.props.actionShowSidebar();
							}}
						/>
						Database
					</label>
					<label
						className={
							this.props.selectedMode == "graph"
								? "btn btn-success border active"
								: "btn btn-success border"
						}
					>
						<input
							type="radio"
							defaultChecked={this.props.selectedMode == "graph"}
							onClick={() => {
								this.props.changeMode("graph");
								this.props.actionShowSidebar();
							}}
						/>
						Grafici
					</label>
				</div>
				{this.props.selectedMode == "raw" ? (
					<p className="my-3">Non ci sono altre impostazioni per la modalià "Database"</p>
				) : (
					""
				)}
				{this.props.selectedMode == "graph" ? (
					<>
						<hr />
						<h6>Visualizza i dati di:</h6>
						<table className="mx-auto mt-2">
							<tbody>
								<SidebarItem
									item={this.props.viewLines.find((x) => x.name == "Italia")}
									viewLines={this.props.viewLines}
									changeState={this.props.changeState}
									key="Italia"
								/>
								{this.props.viewLines.map((item) => {
									if (item.name != "Italia")
										return (
											<SidebarItem
												item={item}
												viewLines={this.props.viewLines}
												changeState={this.props.changeState}
												key={item.name}
											/>
										);
								})}
							</tbody>
						</table>
						<button className="btn btn-small control-panel" onClick={()=>{
							this.props.changeState({
								viewLines: this.props.viewLines.map((item) => {
									return {
										name: item.name,
										show: true,
										color: item.color
									};
								})
							});
						}}><small>Mostra tutti</small></button>
					</>
				) : (
					""
				)}
			</div>
		);
	}
}

export default Sidebar;
