import React from "react";
import "./App.css";
import Control from "./customComponents/Control.js";
import Graphs from "./customComponents/Graphs.js";
import TableRaw from "./customComponents/TableRaw.js";
import Header from "./customComponents/Header.js";
import Sidebar from "./customComponents/Sidebar.js";
import * as Api from "./api.js";

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			data: {},
			viewLines: [],
			viewMode: "graph",
			loading: true,
			error: false,
			showBar: false,
			graphList: ["Nuovi_positivi", "Positivi"],
			startDate: "2020-02-24",
			endDate: "2020-11-31",
			graphData: "STORICO",
			percentage: false,
			fieldsList: [],
		};
	}

	render() {
		return <div className="App container-fluid px-xl-3 vheight-100">{this.iserror()}</div>;
	}

	iserror() {
		if (this.state.error) return <p>Errore con il server</p>;
		else
			return (
				<>
					<Control
						currentSettings={{
							graphList: this.state.graphList,
							startDate: this.state.startDate,
							endDate: this.state.endDate,
							graphData: this.state.graphData,
							percentage: this.state.percentage
						}}
						alterSettings={this.alterDataSettings}
						fieldsList = {this.state.fieldsList}
					/>
					<Header
						mode={this.state.viewMode}
						actionShowSidebar={this.showSidebar}
						viewSidebar={this.state.showBar}
					/>
					{this.state.loading ? (
						<>
							<div className="d-flex justify-content-center mt-5">
								<div className="spinner-border" role="status">
									<span className="sr-only"></span>
								</div>
							</div>
						</>
					) : (
						<>
							{this.state.showBar ? (
								<div className="container-fluid row ml-0 px-0 vheight-100">
									<aside className="col-12 col-md-4 col-xl-3 d-md-block d-flex align-items-stretch mb-3 mb-sm-0">
										<Sidebar
											changeMode={this.changeMode}
											actionShowSidebar={this.showSidebar}
											selectedMode={this.state.viewMode}
											viewLines={this.state.viewLines}
											changeState={this.alterState}
										/>
									</aside>
									<span className="col-xl-9 col-md-8 px-0">{this.viewThing()}</span>
								</div>
							) : (
								<>{this.viewThing()}</>
							)}
						</>
					)}
				</>
			);
	}

	viewThing() {
		switch (this.state.viewMode) {
			case "graph":
				return (
					<div className="row pr-2 pr-xl-5">
						<Graphs
							data={this.state.data}
							viewLines={this.state.viewLines}
							viewPercentages={this.state.percentage}
						/>
					</div>
				);
			case "raw":
				return (
					<>
						<TableRaw name="Dati" data={this.state.data.data} /> <div className="py-5"></div>
						<TableRaw name="Variazione" data={this.state.data.variation} />{" "}
					</>
				);
			default:
				return "";
		}
	}

	showSidebar = () => {
		this.setState((state) => {
			return { showBar: !state.showBar };
		});
	};

	alterState = (edit) => {
		this.setState(edit);
	};

	alterDataSettings = (edit) => {
		this.setState(edit, () => {
			this.changeMode(this.state.viewMode, false);
		});
	};

	componentDidMount() {
		this.changeMode("graph");
		Api.getFieldsList().then(fields => { this.setState({fieldsList: fields.list}); console.log(this.state.fieldsList) });
	}

	changeMode = (mode, changeLines = true) => {
		this.setState({ loading: true });
		if (mode == "raw") {
			Api.getRawData().then((data) => {
				this.setState({ data: data, loading: false, viewLines: [], viewMode: "raw" });
			});
		} else {
			if (mode == "graph") {
				Api.getGraphs(
					this.state.startDate,
					this.state.endDate,
					this.state.graphList,
					this.state.graphData,
					this.state.percentage
				).then((data) => {
					let lines
					if(changeLines) lines = this.getViewLines(data)
					else lines = this.state.viewLines
					this.setState({
						data: data,
						loading: false,
						viewLines: lines,
						viewMode: "graph"
					});
				});
			}
		}
	};

	getViewLines = (data) => {
		let array = Object.keys(data[Object.keys(data)[0]][0]);
		array.splice(array.indexOf("data"), 1);
		return array.map((region) => {
			let color;
			switch (region) {
				case "Abruzzo":
					color = "#C00000";
					break;
				case "Basilicata":
					color = "#FF0000";
					break;
				case "Calabria":
					color = "#C55A11";
					break;
				case "Campania":
					color = "#FF9900";
					break;
				case "Emilia-Romagna":
					color = "#FFD966";
					break;
				case "Friuli-Venezia-Giulia":
					color = "#9FBA16";
					break;
				case "Lazio":
					color = "#548235";
					break;
				case "Liguria":
					color = "#004C00";
					break;
				case "Lombardia":
					color = "#009A46";
					break;
				case "Marche":
					color = "#0A7C66";
					break;
				case "Molise":
					color = "#08B896";
					break;
				case "Piemonte":
					color = "#00B0F0";
					break;
				case "Puglia":
					color = "#0070C0";
					break;
				case "Sardegna":
					color = "#3333CC";
					break;
				case "Sicilia":
					color = "#002060";
					break;
				case "Toscana":
					color = "#7030A0";
					break;
				case "Trentino-Alto-Adige":
					color = "#7030A0";
					break;
				case "Umbria":
					color = "#D60093";
					break;
				case "Valle-d_Aosta":
					color = "#FF00FF";
					break;
				case "Veneto":
					color = "#FF7DFF";
					break;
				default:
					color = "#000";
					break;
			}

			if (region == "Italia") {
				return {
					name: region,
					show: true,
					color: color
				};
			} else {
				return {
					name: region,
					show: false,
					color: color
				};
			}
		});
	};
}

export default App;
